* {
  box-sizing: border-box;
}

:root {
  --color-primary: purple;
  --color-secondary: #afafff;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #383838;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

.stylego {
  width: 100px;
  height: 400px;
  margin-right: 30px;
}

.color {
  color: #3a3a3a;
}

.stylebody {
  background: linear-gradient( to top left, var(--color-primary), var(--color-secondary) );
  height: 1200px;
  max-width: 100%;
  justify-content: center;
  align-items: center;
  display: flex;
}

.styletasktwo {
  text-align: left;
  background: linear-gradient(to right, rgba(255, 255, 255, .09), rgba(255, 255, 255, .02));
  border-radius: 20px;
  padding: 100px;
  box-shadow: 0 8px 24px rgba(0, 0, 0, .3);
}

.styledata {
  height: 50px;
  width: 180px;
  text-align: center;
  color: var(--color-primary);
  background: #b4f6ff;
  border: none;
  border-radius: 8px;
  padding: 10px;
}

.js-btn {
  height: 50px;
  width: 180px;
  background: #b4f6ff;
  border: none;
  border-radius: 8px;
  box-shadow: 0 8px 24px rgba(0, 0, 0, .4);
}

.js-btn:hover {
  background-color: bisque;
}

.timer {
  color: #fff;
  margin-top: 20px;
}

